<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      listorder: []
    }
  },
  methods: {
    itemList: function() {
      this.$http
        .get("http://localhost:1112/ezapi/api/get/getorder.php")
        .then(response => {
          console.log(Response.body);
          this.listorder = response.body;
          console.log(this.listorder);
        });
    }
  },
   created: function() {
    this.itemList();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
